import React, { useState } from 'react'
import './TaskList.scss'
import { Table, Form, Input, Button, Modal } from 'antd'

const CollectionCreateForm = ({ visible, onCreate, onCancel }) => {
  const [form] = Form.useForm()
  const formItemLayout = {
    labelCol: { span: 4 },
  }
  return (
    <Modal
      visible={visible}
      title=""
      onCancel={onCancel}
      okText=""
      cancelText=""
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            form.resetFields()
            onCreate(values)
          })
          .catch(info => {})
      }}
    >
      <Form {...formItemLayout} form={form} name="basic" initialValues={{ remember: true }}>
        <Form.Item label="" name="address" rules={[{ required: true, message: '' }]}>
          <Input placeholder="" />
        </Form.Item>
        <Form.Item label="" name="password" rules={[{ required: true, message: '' }]}>
          <Input placeholder="" />
        </Form.Item>
      </Form>
    </Modal>
  )
}

