import React from 'react'
import './NoduleInfo.scss'
import { Radio, Select, Button } from 'antd'

const { Option } = Select

const NoduleInfo = props => {
  const onLungChange = e => {
    props.checkNoduleList(e.target.value, 'lung')
    if (e.target.value === '' && props.noduleInfo.lobe === '') {
      props.checkNoduleList('', 'lobe')
    }
  }

  const onLobeChange = e => {
    props.checkNoduleList(e.target.value, 'lobe')
  }

  const handleSelectChange = val => {
    props.checkNoduleList(val, 'type')
  }

  return (
    <div className="nodule-info-box">
      {props.noduleInfo ? (
        <div className="nodule-detail">
          {/* <div className="list">
            <em></em>
            {props.noduleInfo.num}
          </div> */}
          <div className="list">
            <span className="list-title"></span>
            <Radio.Group onChange={onLungChange} value={props.noduleInfo.lung}>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
            </Radio.Group>
          </div>
          <div className="list">
            <div className="list-title"></div>
            <Radio.Group onChange={onLobeChange} value={props.noduleInfo.lobe}>
              <Radio value={''}></Radio>
              {props.noduleInfo.lung === '' ? null : <Radio value={''}></Radio>}
              <Radio value={''}></Radio>
            </Radio.Group>
          </div>
          <div className="list">
            <div className="list-title"></div>
            <Select
              size="small"
              value={props.noduleInfo.type}
              style={{ width: 150 }}
              onChange={handleSelectChange}
            >
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </div>
          <div className="list">
            <em></em>
            {props.noduleInfo.size}
          </div>
          <div className="list">
            <em></em>
            {props.noduleInfo.noduleSize} mm
          </div>
          <div className="list">
            <em></em>
            {props.noduleInfo.type}
          </div>
        </div>
      ) : null}
      {props.noduleInfo ? (
        <div className="check-group">
          <div className="group-wrap">
            <span></span>
            <div className="group">
              <Button style={{ marginRight: '10px' }} size="small" onClick={e => props.updateNoduleList(false)}>
                
              </Button>
              <Button size="small" onClick={e => props.updateNoduleList(true)} type="primary">
                
              </Button>
            </div>
          </div>
        </div>
      ) : null}
    </div>
  )
}

export default NoduleInfo
